.popup {
	width: 338px;
	padding-top: 45px;
	border-radius: 10px;
	background: #fff;
	font-weight: 700;
	position: fixed;
	top: 50%;
	left: 50%;
	transform:  translate(-50%, -50%);
	z-index: 10;
	box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.2); }
.popup-close {
	position: absolute;
	top: 15px;
	right: 15px;
	padding: 10px;
	cursor: pointer; }
.bg_popup-img {
	position: absolute;
	top: 57px;
	left: 0px;
	z-index: -1;
	margin: 0 10px; }
.popup_title {
	color: #231e1f;
	font-size: 26px;
	line-height: 1.2;
	width: 265px;
	text-align: center;
	margin: 0 auto;
	span {
		font-weight: 600; } }
.popup_subtitle {
	color: #231e1f;
	margin-bottom: 10px; }
.form-input, .form-textarea {
	width: 270px;
	border: 1px solid #c6c6c6;
	border-radius: 10px; }
.form-input {
	height: 40px;
	margin-bottom: 15px; }
.form-textarea {
	height: 90px; }

.form-submit {
	display: block;
	width: 270px;
	font-size: 14px;
	font-weight: 700;
	color: #fff;
	border-radius: 10px;
	border: none;
	padding: 10px 20px;
	margin: 0 auto;

 }	//::::::::::::: bg-popup:::::::::::::
.bg-popup {
	position: absolute;
	display: none;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background: rgba(151,151,151, .7);
	z-index: 7;
	@media(min-width:992px) {
 } }		// display: none!important

.popup_discount {
	display: none;
	height: 325px;
	.form-submit {
		margin-bottom: 40px;
		background-color: #b8a4dd;
		&:focus, &:active, &:hover {
			background-color: darken(#b8a4dd, 10%); } } }

.popup_recall {
	display: none;
	padding-bottom: 50px;
	.popup_title {
		margin-bottom: 35px; }
	.form-submit {
		// margin-bottom: 50px
		background-color: $red;
		&:focus, &:active, &:hover {
			background-color: darken($red, 10%); } } }

#captcha {
	margin: 5px auto 17px;
	a {
		background-color: #dadada;
		padding: 25px 20px;
		color:#666666 {}

		&:focus, &:active, &:hover {
			background-color: darken(#dadada, 10%); } } }

.popup_enter {
	display: none;
	.form-submit {
		// margin-bottom: 50px
		background-color: $green;
		&:focus, &:active, &:hover {
			background-color: darken($green, 10%); } }
	.btn_sosial-wrapper {
		width: 300px;
		margin: 0px auto 35px;
		display: flex;
		justify-content: space-between;
		.btn_sosial {
			width: 143px;
			margin: 0;
			font-weight: 500;
			font-size: 12px; }
		#btn_facebook {
			background-color: #3b5a94;
			&:focus, &:active, &:hover {
				background-color: darken(#3b5a94, 10%); } }
		#btn_google {
			background-color: #f9463e;
			&:focus, &:active, &:hover {
				background-color: darken(#f9463e, 10%); } } }
	#psw_restore {
		display: inline-block;
		font-size: 12px;
		color: #231e1f;
		padding: 10px;
		cursor: pointer; }
	.registration {
		height: 75px;
		border-top: 1px solid #919191;
		padding-top: 20px;
		span {
			color: $green;
			text-transform: uppercase;
			padding: 10px;
			cursor: pointer;
			&:focus, &:active, &:hover {
				color: darken($green, 10%); } } } }
