.blog-open {
    // display: none
    padding: 20px 50px 40px;
    @media(min-width:992px) {
        padding: 20px 50px 40px; }
    .blog-item_title {
        max-width: 475px;
        margin: 0 auto; }
    h3 {
        color: #231e1f;
        font-size: 14px;
        margin-bottom: 20px; } }
.banner {
    img {
        width: 100%; } }
.blog-text {
    padding: 0 35px 20px;
    margin-top: 30px;
    p {
        color: #706f6f;
        font-size: 14px;
        font-weight: 400;
        line-height: 1.4;
        margin-bottom: 20px; } }
.blog-text-middle {
    display: flex;
    margin: 40px 0 20px; }
.float-img {
    width: 30%;
    height: 100%;
    margin-right: 80px; }
.blog-galery {
    display: flex;
    align-items: stretch;
    justify-content: center;
    padding: 0 35px;
    img {
        width: 100%; } }
.blog-arrows {
    a {
        display: block; }
    display: flex;
    flex-wrap: wrap;
    width: 330px;
    margin: 30px auto;
    color: #000000;
    font-size: 12px;
    img {
        width: 26px;
        margin: 0 6px; } }
.blog-arrow-prev, .blog-arrow-next {
    width: 50%; }
.blog-arrow-next {
    border-left: 1px solid #000000; }
.blog-return {
    margin: 0 auto;
    a {
        padding: 10px; } }
