.card-block-wrapper {
 }    // display: none
.card-block {
    text-align: left;
    padding: 15px 15px;
    @media(min-width:576px) {
 }        // padding: 15px 45px
    @media(min-width:992px) {
        padding: 10px 30px; }
    @media(min-width:1200px) {
        padding: 10px 70px; } }
.card-block-top {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    color: #231e1f;
    font-size: 14px;
    margin-bottom: 20px;

    @media(min-width:576px) {
        padding: 0 45px; }
    @media(min-width:992px) {
        padding: 0;
        flex-direction: row;
        align-items: center; } }
.path {
    text-transform: uppercase; }
.prod-code {
    padding: 10px;
    margin-top: 25px;
    max-height: 40px;
    min-width: 155px;
    border: 1px solid #b8a4dd;
    background-color: #ededed;
    @media(min-width:992px) {
        margin-top: 0; } }
.card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @media(min-width:992px) {
        flex-direction: row; }
    .slick-arrow {
        display: none!important;
        @media(min-width:768px) {
            display: block!important; } } }

.card-sliders {
    margin: 0 auto;
    @media(min-width:992px) {
        margin: 0; }
    .card-slide {
        outline: none;
        cursor: pointer;
        img {
            margin: 0 auto;
            border-radius: 15px; } } }
.card-slider-top {
    width: 450px;
    height: 400px;
    border: 1px solid #c6c6c6;
    border-radius: 15px;
    margin-bottom: 13px; }
.card-slider-bottom {
    width: 450px;
    // height: 85px
    .card-slide {
        width: 85px;
        height: 85px;
        border: 1px solid #c6c6c6;
        border-radius: 15px;
        z-index: 1;
        margin-right: 3px;
        &:last-child {
            margin-right: 0px; }
        img {
            width: 100%;
            margin: 0 auto;
            z-index: -1; } } }
.card-info {
    width: 450px;
    margin: 0 auto;
    @media(min-width:992px) {
        margin: 0;
        width: 48%; } }

.card-name {
    width: 430px;
    color: #3c3c3b;
    font-size: 24px;
    margin: 10px 0 15px;
 }    // font-weight: 600
.card-code {
    color: #b2b2b2;
    margin-bottom: 20px;
    @media(min-width:992px) {
        margin-bottom: 0; } }
.card-price {
    color: #3c3c3b;
    font-size: 22px;
    font-weight: 700;
    margin: 15px 0; }
.card-warranty {
    color: #666666; }
.card-buttons {
    display: flex;
    align-items: center;
    margin: 20px 0 60px;

    @media(min-width:992px) {
        margin: 30px 0; }
    .form-submit {
        width: 170px;
        height: 50px;
        font-size: 22px;
        background-color: $green;
        margin: 0 30px 0 0; }
    .to-wishes {
        cursor: pointer;
        span {
            color: #878787;
            border-bottom: 1px dotted #333;
            margin-left: 5px; } } }

.card-table {
    width: 375px;
    border: 1px solid #c6c6c6;
    ol {
        list-style-type: none;
        counter-reset: num; }

    li {
        font-size: 14px;
        margin: 10px 0;
        padding-top: 5px;
        position: relative;

        &::before {
            position: absolute;
            left: -20px;
            content: counter(num) '. ';
            counter-increment: num;
            color: #b8a4dd;
            font-weight: 700;
            padding-right: 10px; } } }

.card-table-title {
    border-bottom: 1px solid #c6c6c6;
    color: #231e1f;
    padding: 10px 40px;
    position: relative;
    &::before {
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
        content: '';
        width: 8px;
        height: 8px;
        border-radius: 4px;
        background-color: $green;
        // font-weight: 900
        // font-size: 64px
 } }        // padding-right: 10px
.card-tabs-wrapper {
    text-align: left; }
.card-tabs {
    display: flex;
    flex-direction: column;
    margin: 20px;
    @media(min-width:768px) {
        flex-direction: row;
        margin: 50px 70px 10px; } }
.card-tab {
    margin-right: 40px;
    margin-top: 5px;
    span {
        cursor: pointer;
        font-size: 18px;
        font-weight: 600;
        border-bottom: 1px dotted #333;
        @media(min-width:768px) {
            font-weight: 400; } } }
.card-tab.active {
    span {
        border: none;
        color: $red; } }
.tabs-content {
    border-top: 1px solid #c6c6c6;
    border-bottom: 1px solid #c6c6c6;
    background-color: #f7f4ec;
    padding: 30px 20px;
    @media(min-width:768px) {
        padding: 30px 90px 70px 70px; } }
.views {
    border-top: 1px solid #c6c6c6;
    @media(min-width:768px) {
        border-top: none;
        .product-slider-wrapper {
            margin: 0; }
        .product-code, .product-rating, .product-like {
            display: none; }
        .product-item {
            position: relative;
            width: 370px;
            height: 190px;
            padding: 0; }
        .product-img {

            width: 50%;
            height: 188px;
            margin: 0; }
        .product-title {
            position: absolute;
            left: 50%;
            top: 40px;
            width: 150px;
            color: #3c3c3b;
            font-size: 11px;
            font-weight: 500;
            margin: 0; }
        .product-bottom {
            position: absolute;
            left: 50%;
            top: 70px;
            flex-direction: column;
            margin: 0; } } }
