.product-slider-wrapper {
    margin: 50px 65px;
    h2 {
        text-align: left;
        margin-bottom: 45px;
        @media(min-width:992px) {
            margin-bottom: 25px; } }
    @media(min-width:768px) {
        margin: 70px 85px 40px; } }
.product {
    outline: none; }

.product-item {
    width: 330px;
    text-align: left;
    padding: 5px 12px 15px;
    border: 1px solid #c6c6c6;
    margin: 0 auto;
    @media(min-width:768px) {
        width: 250px; } }
.product-img {
    // border-bottom: 1px solid #c6c6c6
    margin-bottom: 15px;
    height: 280px;
    @media(min-width:768px) {
        height: 220px; }
    img {
        margin: 0 auto;
        max-height: 100%; } }

.product-code {
    color: #c6c6c6;
    font-size: 11px;
    font-weight: 400; }
.product-title {
    width: 200px;
    font-size: 13px;
    font-weight: 700;
    margin: 5px 0; }
.product-rating {
    display: flex;
    align-items: center; }
.product-stars {
    margin-right: 10px; }
.product-comments {
    color: #0091d6;
    font-size: 11px;
    font-weight: 400;
    text-decoration: underline; }
.product-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #3c3c3b;
    font-weight: 700;
    margin-top: 20px; }
.product-price_num {
    font-size: 25px;
    @media(min-width:768px) {
        font-size: 19px; } }
.product-price_cur {
    font-size: 13px; }

.product-like {
    width: 22px;
    margin-left: 30px;
    @media(min-width:768px) {
        width: auto; } }
.product-buy {
    background-color: $green;
    color: #fff;
    border-radius: 10px;
    border: none;
    width: 88px;
    height: 35px;
    font-size: 19px;
    font-weight: 700;
    padding-left: 5px; }
.product-slider-prev {
    left: -20px;
    transform: translate(-50%, -50%); }
.product-slider-next {
    right: -20px;
    transform: translate(50%, -50%); }
